import React, {Component} from "react";
import {OraSimpleRadio} from "eshop/components/OraCommonComponents";
import {MobileBillingAccount} from "eshop/modules/auth/components/MobileBillingAccount";
import {MobileBillingAccountTitle} from "eshop/modules/auth/components/MobileBillingAccountTitle";
import BillingAccountSearchComponent from "eshop/modules/auth/components/BillingAccountSearchComponent";

export class BillingAccountSelectionSectionB2BComponent extends Component {

    constructor(props) {
        super(props);
        this.onClickNewAccount = this.onClickNewAccount.bind(this);
       }

    onClickNewAccount() {
        this.props.setSelectedBillingAccount("","");
    }

    getPropsForRadio(text, value) {
        return {
            name: "accountSelect",
            text: text,
            value: value,
            labelClassName: "o-radio opl-radio",
            readOnly: false,
            checked: !!(value == this.props.selectedBillingAccountId),
            onChange: this.onClickNewAccount
        };
    }


    channelIsWWW() {
        return !!this.props.channels && this.props.channels.sales === 'WWW';
    }


    render() {
        return (
            <div>
                <fieldset>
                    <div className="g-gray1-bg u-padding-all-l">
                        <p className="h3 u-no-margin">Wybierz konto</p>
                    </div>
                    {this.props.isBillingAccountContractLimitExceeded &&
                    <BillingAccountSearchComponent {...this.props}/>
                    }
                    <div className="u-padding-all-l">
                        <div className="u-margin-m">
                            {!this.channelIsWWW() &&
                            <OraSimpleRadio {...this.getPropsForRadio((this.props.descriptions && this.props.descriptions.useNewAccountLabel || "StwÃ³rz nowe konto abonamentowe"), "")} />
                            }
                        </div>
                        <div className="o-separator u-margin-m"></div>
                        {
                            this.props.filteredAccounts && this.props.filteredAccounts.length > 0 && this.props.filteredAccounts.map(billAccount => [
                                <MobileBillingAccountTitle
                                    key={billAccount.id}
                                    selectedBillingAccountId={this.props.selectedBillingAccountId}
                                    setSelectedBillingAccount={this.props.setSelectedBillingAccount}
                                    account={billAccount}/>
                                , <MobileBillingAccount account={billAccount}/>
                            ])
                        }
                    </div>
                    <div className="g-gray1-bg u-padding-all-l">
                        <div className="l-row">
                            <div className="l-col l-col-3 large-offset-by-9 small-offset-by-9 medium-offset-by-9">
                                <button className="opl-btn opl-btn--primary o-btn u-w-100"
                                        onClick={this.props.selectAccount}>
                                    <span className="opl-ripple-box">
                                        <span className="opl-ripple-circle"></span>
                                    </span>
                                    Wybierz
                                </button>
                            </div>
                        </div>
                    </div>
                </fieldset>
            </div>

        )
    }
}
